import React, { useEffect, useState, useRef } from 'react';
import { getCommits } from './api';
import { Chart } from 'chart.js';

const CommitGraph = ({ username, repo }) => {
  const [data, setData] = useState([]);
  const chartRef = useRef(null);

  useEffect(() => {
    if (username && repo) {
      getCommits(username, repo)
        .then((response) => {
          const commitData = response.data.map((commit, index) => ({
            name: commit.sha.substring(0, 7),
            commits: 1,
            index: index + 1
          }));

          setData(commitData);
        });
    }
  }, [username, repo]);

  useEffect(() => {
    if (data.length > 0 && chartRef.current) {
      const labels = data.map(commit => commit.name);
      const commits = data.map(commit => commit.commits);

      const chartConfig = {
        type: 'line',
        data: {
          labels: labels,
          datasets: [
            {
              label: 'Commits',
              data: commits,
              borderColor: 'blue',
              backgroundColor: 'transparent'
            }
          ]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          scales: {
            y: {
              beginAtZero: true,
              stepSize: 1
            }
          }
        }
      };

      new Chart(chartRef.current, chartConfig);
    }
  }, [data]);

  return (
    <div>
      <canvas ref={chartRef} width={400} height={200} />
    </div>
  );
};

export default CommitGraph;
